import "themes/bootstrap-theme.scss";
import "./index.scss";

// Support IE 11+
import "react-app-polyfill/ie11";
import "react-app-polyfill/stable";

import React, { useState, useEffect, Suspense, lazy } from "react";
import ReactDOM from "react-dom";
import * as serviceWorker from "./serviceWorker";

import {
  BrowserRouter as Router,
  useRouteMatch,
  Route,
  Switch,
  Link,
} from "react-router-dom";
import { createIntl, createIntlCache, RawIntlProvider } from "react-intl";

import { Helmet } from "react-helmet";

import TagManager from "react-gtm-module";

import Loading from "components/Loading";

export const AppContext = React.createContext();

const supportedLocales = [
  "en",
  // "en-gb",
  // "es",
  // "de",
  // "fr",
  // "ar",
  // "ru",
  // "zh-cn",
  "zh-tw",
  // "ja",
  // "ko"
];
const defaultLocale = "en";

// pages
const Home = lazy(() => import("routes/Home"));
const Faq = lazy(() => import("routes/Faq"));
const Privacy = lazy(() => import("routes/Privacy"));
const Terms = lazy(() => import("routes/Terms"));
const Acknowledgements = lazy(() => import("routes/Acknowledgements"));
const NotFound = lazy(() => import("routes/NotFound"));

function App() {
  // Set up states
  const match = useRouteMatch();
  let locale = match.params.locale;
  if (!locale) {
    locale = navigator.language || navigator.userLanguage; // for IE
    locale = locale.toLowerCase();
    if (!supportedLocales.includes(locale)) {
      locale = locale.split(/[-_]/)[0];
      if (!supportedLocales.includes(locale)) locale = defaultLocale;
    }
  }
  const [currentLocale, setCurrentLocale] = useState(locale);
  const [messages, setMessages] = useState();
  useEffect(() => {
    if (locale !== defaultLocale) {
      setCurrentLocale(locale);
      setMessages(null);
      import(`i18n/${locale}.js`).then((module) => {
        setMessages(module.default);
      });
    }
  }, [locale]); // update `currentLocale` only if `locale` changes

  if (locale !== defaultLocale && !messages) return <Loading />;

  // Set up React-intl
  const cache = createIntlCache();
  const intl = createIntl(
    {
      locale: currentLocale,
      messages: messages,
    },
    cache
  );

  // Set up client-side routing
  let prefix = match.url;
  if (!prefix.endsWith("/")) prefix = prefix + "/";

  // Set up AppContext value
  const appContext = {
    supportedLocales: supportedLocales,
    currentLocale: currentLocale,
    intl: intl,
    urlPrefix: prefix,
  };

  return (
    <AppContext.Provider value={appContext}>
      <RawIntlProvider value={intl}>
        <Helmet>
          <title>
            {intl.formatMessage({
              id: "index.title",
              defaultMessage: "Flora - Green Productivity",
            })}
          </title>
          <meta name="author" content="AppFinca Inc." />

          {/* Google SEO */}
          <meta
            name="description"
            content={intl.formatMessage({
              id: "index.description",
              defaultMessage:
                "Flora is a free, eco-friendly productivity app that helps you stay focused on your tasks, clear to-do lists, and build positive, life-changing habits. Whenever you want to make progress towards your goals, grow tree in Flora!",
            })}
          />
          <link
            rel="alternate"
            href="https://flora.appfinca.com"
            hreflang="x-default"
          />
          {supportedLocales.map((locale, index) => (
            <link
              key={index}
              rel="alternate"
              href={"https://flora.appfinca.com/" + locale}
              hreflang={locale}
            ></link>
          ))}
          <script type="application/ld+json">
            {`{
            "@context": "http://schema.org",
            "@type": "WebSite",
            "name": "${intl.formatMessage({
              id: "index.title",
              defaultMessage: "Flora - Green Productivity",
            })}",
            "url": "https://flora.appfinca.com",
            "logo": "https://flora.appfinca.com/static/media/icon-512.png",
            "contactPoint" : [{
              "@type" : "ContactPoint",
              "url": "https://flora.appfinca.com/faq",
              "email": "support@flora.appfinca.com",
              "contactType": "customer support"
            }],
            "sameAs": [
              "https://www.facebook.com/FloraAppfinca",
              "https://twitter.com/FloraApp"
            ]
          }`}
          </script>
          <script type="application/ld+json">
            {`{
            "@context": "http://schema.org",
            "@type": "Country",
            "address": {
              "addressCountry": "US"
            }
          }`}
          </script>

          {/* Facebook Open Graph */}
          <meta property="fb:app_id" content="100749660278765" />
          <meta property="og:type" content="website" />
          <meta
            property="og:url"
            content={"https://flora.appfinca.com" + prefix}
          />
          <meta property="og:site_name" content="Flora" />
          <meta
            property="og:title"
            content={intl.formatMessage({
              id: "index.social.title",
              defaultMessage: "Flora - Green Productivity",
            })}
          />
          <meta
            property="og:description"
            content={intl.formatMessage({
              id: "index.social.description",
              defaultMessage:
                "A new way to focus on your tasks, clear to-do lists, and build good habits.",
            })}
          />
          <meta
            property="og:image"
            content="https://flora.appfinca.com/static/media/social.png"
          />
          <meta property="og:locale" content={locale} />

          {/* Twitter Card */}
          <meta name="twitter:card" content="summary_large_image" />
          <meta name="twitter:site" content="@FloraApp" />
          <meta
            name="twitter:title"
            content={intl.formatMessage({
              id: "index.social.title",
              defaultMessage: "Flora - Green Productivity",
            })}
          />
          <meta
            name="twitter:description"
            content={intl.formatMessage({
              id: "index.social.description",
              defaultMessage:
                "A new way to focus on your tasks, clear to-do lists, and build good habits.",
            })}
          />
          <meta
            name="twitter:image"
            content="https://flora.appfinca.com/static/media/social.png"
          />
        </Helmet>
        <Suspense fallback={<Loading small />}>
          <Switch>
            <Route exact path={`${prefix}`} component={Home} />
            <Route exact path={`${prefix}faq`} component={Faq} />
            <Route exact path="/privacy" component={Privacy} />
            <Route exact path="/privacy/index.html" component={Privacy} />
            <Route exact path="/terms" component={Terms} />
            <Route exact path="/terms/index.html" component={Terms} />
            <Route
              exact
              path="/acknowledgements"
              component={Acknowledgements}
            />
            <Route component={NotFound} />
          </Switch>
        </Suspense>
        {/* Link to all URLs for react-snap to crawl */}
        <div>
          {/* This extra div is necessary for react-snap to work */}
          <div className="d-none">
            <Link to="/">/</Link>
            <Link to="/faq">/faq</Link>
            <Link to="/privacy">/privacy</Link>
            <Link to="/privacy/index.html">/privacy</Link>
            <Link to="/terms">/terms</Link>
            <Link to="/terms/index.html">/terms</Link>
            <Link to="/acknowledgements">/acknowledgements</Link>
            {supportedLocales.map((locale, index) => (
              <Link
                key={`/${locale}/`}
                to={`/${locale}/`}
              >{`/${locale}/`}</Link>
            ))}
            {supportedLocales.map((locale, index) => (
              <Link
                key={`/${locale}/faq`}
                to={`/${locale}/faq`}
              >{`/${locale}/faq`}</Link>
            ))}
          </div>
        </div>
      </RawIntlProvider>
    </AppContext.Provider>
  );
}

const rootEl = document.getElementById("root");
let render = ReactDOM.render;
if (rootEl.hasChildNodes()) {
  render = ReactDOM.hydrate;
}
render(
  <Router>
    <Switch>
      <Route path={`/:locale(${supportedLocales.join("|")})`} component={App} />
      <Route component={App} />
    </Switch>
  </Router>,
  rootEl
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();

// Google Tag Manager. Add Google Universal Analytics tags or Google Ads
// Conversion Tracking tags on GTM.
TagManager.initialize({
  gtmId: "GTM-PZQ6LWF",
});
